@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
.grid {
    display: grid;
    grid-gap: 32px;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    justify-items: center;
    margin-top: 2.5em;
  }
  
  .grid figure {
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 480px;
    min-width: 320px;
    height: auto;
    background: #04080f;
    text-align: center;
    border-radius: 8px;
    cursor: default;
  }
  
  figure:hover figcaption,
  figure:hover p.icon-links a {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  
  .grid figure figcaption,
  .grid figure figcaption > a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .image-background {
    -webkit-transition: opacity 0.7s, -webkit-transform 0.7s;
    transition: opacity 0.7s, -webkit-transform 0.7s;
    transition: opacity 0.7s, transform 0.7s;
    transition: opacity 0.7s, transform 0.7s, -webkit-transform 0.7s;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    width: 100%;
    height: 100%;
  }
  
  figure:hover .image-background {
    opacity: 0.3;
    -webkit-transform: scale(1.4);
    transform: scale(1.4);
  }
  
  .project-logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .projects-logo {
    position: absolute;
    bottom: 35%;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    z-index: 1;
    -webkit-transform: scale(1.2);
    -webkit-transform: translate3d(0, 35%, 0);
    -webkit-transition: opacity 0.7s, -webkit-transform 0.7s;
    -webkit-backface-visibility: hidden;
    transition: opacity 0.7s, -webkit-transform 0.7s;
    transition: opacity 0.7s, transform 0.7s;
    transition: opacity 0.7s, transform 0.7s, -webkit-transform 0.7s;
  }
  
  figure:hover .projects-logo {
    -webkit-transition-delay: 0.05s;
    transition-delay: 0.05s;
    transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
  
  .dream-logo,
  .smart-logo,
  .trivia-logo {
    width: 240px;
    height: 100%;
  }
  
  .show-logo {
    width: 180px;
    height: 100%;
  }
  
  figure figcaption {
    top: auto;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    color: #3c4a50;
    -webkit-transition: -webkit-transform 0.7s;
    transition: -webkit-transform 0.7s;
    transition: transform 0.7s;
    transition: transform 0.7s, -webkit-transform 0.7s;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  
  .bottom-container {
    height: 64px;
    background: #c5c6c7;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-radius: 0px 0px 8px 8px;
  }
  
  .portfolio-links {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-content: center;
  }
  
  .bottom-container {
    align-self: flex-end;
    width: 100%;
  }
  
  .project-button {
    display: inline-block;
    color: white;
    letter-spacing: 1.2px;
    border: none;
    border-radius: 8px;
    padding: 12px 24px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    outline: none;
    font-size: 14px;
    text-decoration: none;
    background-color: #1f2833;
  }
  
  .project-button:hover {
    cursor: pointer;
    background-color: #45a29e;
  }
  
  figure p.description {
    position: absolute;
    top: 30%;
    padding: 1em;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 1px;
    color: #fff;
    text-transform: none;
    opacity: 0;
    -webkit-transition: opacity 0.7s;
    transition: opacity 0.7s;
    -webkit-backface-visibility: hidden; /* Fix for Chrome 37.0.2062.120 (Mac) */
    width: 100%;
  }
  
  figure:hover p.description {
    opacity: 1;
  }
  
  @media only screen and (max-width: 600px) {
    figure.effect .image-background {
      opacity: 0.3;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  
    figure.effect p.description {
      opacity: 1;
    }
  
    figure.effect figcaption,
    figure.effect h2,
    figure.effect p.icon-links a {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  
    .projects-logo {
      transform: translate3d(0, 0, 0);
      -webkit-transform: translate3d(0, 0, 0);
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
    }
  
    figure.effect p.description {
      font-size: 14px;
    }
  }
  
  @media only screen and (max-width: 480px) {
    .grid {
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    }
  
    .grid figure {
      min-width: 280px;
      min-height: 250px;
    }
  }
.contact-form {
    position: absolute;
    width: 100%;
    height: 100%;
    max-width: 600px;
    max-height: 500px;
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    margin-top: 45px;
  }

  .section-header {
    margin-top: 30px;
  }
  
  .name,
  .email,
  .message {
    background-color: #1f2833;
    border: none;
    font-family: "Open Sans", sans-serif;
    color: white;
    padding-left: 16px;
    padding-right: 16px;
    margin-right: 1px;
    margin-left: 1px;
    margin-bottom: 1em;
  }
  
  ::-webkit-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  ::-moz-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  :-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  ::-ms-input-placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  ::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
  }
  
  .name,
  .email {
    min-height: 56px;
  }
  
  .name {
    border-radius: 8px 8px 0 0;
  }
  
  .email {
    border-radius: 0;
  }
  
  .message {
    height: 100%;
    padding-top: 1em;
    resize: none;
    border-radius: 0 0 8px 8px;
    line-height: 28px;
  }
  
  .buttonInline {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .buttonInline input:disabled {
    background-color: #45a29e;
    cursor: default;
  }
  
  textarea:focus,
  input:focus,
  input[type]:focus,
  .uneditable-input:focus {
    -webkit-appearance: none;
    box-shadow: 0 0 1pt 1pt #45a29e;
    -webkit-box-shadow: 0 0 1pt 1pt #45a29e;
    -moz-box-shadow: 0 0 1pt 1pt #45a29e;
  }
  
  p.active {
    margin-right: 24px;
    display: block;
    letter-spacing: 1.1px;
    color: white;
  }
  
  p.inactive {
    display: none;
  }
  
  span.email-text {
    font-size: 1.2rem;
    font-weight: 400;
    color: #66fcf1;
    text-decoration: none;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
    width: 300px;
    line-height: 1.8em;
  }
  
  .email-text:hover {
    color: #45a29e;
    cursor: pointer;
  }
  
  #tooltip {
    color: white;
    background-color: #45a29e;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 6px 12px;
  }
  
  .rc-tooltip-arrow {
    border-bottom-color: #45a29e !important;
  }
  
  .peace-emoji {
    font-size: 24px;
  }
  
  @media only screen and (max-width: 1000px) {
    .contact-form {
      margin-bottom: 64px;
      height: 450px;
      position: static;
      padding: 0;
    }
  }
i {
    font-size: 40px;
  }
  
  .skills-list {
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    height: 100%;
    max-width: 600px;
    max-height: 600px;
    padding: 0 0 15% 15%;
  }
  
  .skills-list li {
    display: flex;
    justify-content: space-around;
    color: white;
    align-content: center;
    flex-direction: column;
    text-align: center;
  }
  
  .skills-list i {
    padding-top: 18px;
  }
  
  .skills-list p {
    padding-bottom: 18px;
  }
  
  .skills-list li:nth-child(odd) {
    background-color: #1f2833;
  }
  
  @media only screen and (max-width: 1000px) {
    .skills-list {
      height: 480px;
      width: 480px;
      position: static;
      padding: 5%;
      max-width: 500px;
      max-height: 500px;
    }
  }
  
  @media only screen and (max-width: 590px) {
    .skills-list {
      padding: 0;
    }
  }
  
  @media only screen and (max-width: 480px) {
    .skills-list {
      height: 100vw;
    }
  
    i {
      font-size: 32px;
    }
  
    .skills-list i {
      padding-top: 16px;
    }
  
    .skills-list p {
      padding-bottom: 16px;
    }
  }
.skills input {
    margin-top: 40px;
  }
  
  .skills a {
    color: #66fcf1;
    text-decoration: none;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
  }
  
  .skills a:hover {
    color: #45a29e;
  }
  
  @media only screen and (max-width: 480px) {
    .skills input {
      margin-top: 24px;
    }
  }
.nav-list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
  }
  
  .nav-list li {
    list-style: none;
    font-size: 1rem;
    font-weight: 400;
    text-align: center;
    letter-spacing: 1px;
  }
  
  .link:not(:last-of-type) {
    margin-right: 4em;
  }
  
  .link a {
    color: white;
    text-decoration: none;
  }
  
  .link a:hover {
    cursor: pointer;
    color: white;
  }
  
  .link a:after {
    content: "";
    position: relative;
    width: 0;
    height: 2px;
    display: block;
    margin-top: 5px;
    right: 0;
    background: #66fcf1;
    transition: width 0.5s ease;
    -webkit-transition: width 0.5s ease;
    border-radius: 600px;
  }
  
  .link a:hover:after {
    width: 100%;
    background: #66fcf1;
  }
  
  .logo {
    position: relative;
    margin-right: auto;
    width: 48px;
  }
  
  .logo-container {
    display: flex;
  }
  
  .logoWhite {
    position: relative;
    z-index: 1;
    cursor: pointer;
  }
  
  .logoGreen {
    position: absolute;
    top: 0px;
    z-index: 0;
    left: 0px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  
  .logoWhite:hover ~ .logoGreen {
    top: 6px;
    left: 10px;
  }
  
  @media only screen and (max-width: 800px) {
    .nav-list {
      height: 64px;
    }
  
    .full-nav {
      height: 64px;
    }
  
    .logoGreen {
      display: none;
    }
  
    .link {
      display: none;
    }
  }
#page-wrap {
    overflow: hidden;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
    text-decoration: none;
    font-size: 1.2rem;
    color: white;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
    outline: none;
    letter-spacing: 1.1px;
  }
  
  nav {
    background-color: #04080f;
    overflow: hidden;
  }
  
  .menu {
    overflow: hidden;
  }
  
  .menu a {
    display: block;
    padding: 20px 32px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
  
  .menu a:first-of-type {
    margin-top: 40px;
  }
  
  .menu a:nth-child(odd) {
    background-color: #1f2833;
  }
  
  .menu a:hover {
    background-color: #45a29e;
    cursor: pointer;
  }
  
  /* Position and sizing of burger button */
  .bm-burger-button {
    position: fixed;
    width: 44px;
    height: 38px;
    right: 5%;
    top: 13px;
  }
  
  .bm-burger-button button {
    outline: none;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: white;
  }
  
  /* General sidebar styles */
  
  /* Color/shape of close button cross */
  .bm-cross-button button {
    background-color: white;
    color: white;
  }
  
  @media only screen and (min-width: 800px) {
    .bm-burger-button {
      display: none;
    }
  }
body {
  width: 100%;
  height: 100%;
  background-color: #04080f;
}

.linkedin-github {
  margin: 60px;
}

.linkedin,
.github {
  margin: 10px;
  color: white;
}

#outer-container {
  width: 100%;
  height: 100%;
  padding: 0 5% 0 5%;
}

.particles-js {
  z-index: -1;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

header {
  height: calc(100vh - 64px);
  max-height: 880px;
  margin-top: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.full-nav {
  top: 0;
  left: 0;
  height: 80px;
  width: 100%;
  position: fixed;
  z-index: 1;
  background-color: rgb(4, 8, 15, 0.9);
  padding: 0 5% 0 5%;
}

.header-content {
  margin: auto;
  height: 40vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
}

.header-name {
  text-align: center;
  color: #66fcf1;
  font-size: 4em;
  letter-spacing: 1.2px;
  cursor: default;
}

header input {
  margin-top: 80px;
}

.subheader {
  text-align: center;
  color: white;
  font-weight: 100;
  letter-spacing: 1.2px;
  font-size: 2.2rem;
  margin-top: 2rem;
  cursor: default;
}

.standard-button {
  background-color: transparent;
  border: 1.5px solid #66fcf1;
  display: inline-block;
  color: white;
  border-radius: 8px;
  padding: 1em 1.4em;
  transition: background-color 0.5s ease;
  -webkit-transition: background-color 0.5s ease;
  outline: none;
  font-size: 1em;
  font-weight: 600;
  letter-spacing: 1.2px;
  max-width: 12em;
}

.standard-button:hover {
  background-color: #45a29e;
  color: white;
  cursor: pointer;
}

section h1 {
  color: #66fcf1;
  font-size: 3em;
  font-weight: 800;
}

.left-content p {
  width: 90%;
  margin-top: 2em;
  color: white;
  font-size: 1.2em;
  font-weight: 400;
  line-height: 1.8em;
  letter-spacing: 1.2px;
}

.skills,
.contact {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  min-height: 600px;
}

.skills {
  margin-bottom: 50vh;
  margin-top: 50vh;
}

.projects {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 50vh;
}

.left-content {
  width: 50%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  height: 100%;
}

.skills-content,
.contact-content {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  position: relative;
  max-height: 660px;
}

.skills-content:before,
.contact-content:before {
  content: "";
  display: block;
  padding-top: 100%; /* initial ratio of 1:1*/
}

@media only screen and (max-width: 1000px) {
  .left-content {
    width: 100%;
  }

  .left-content p {
    margin-top: 2rem;
    font-size: 14px;
    width: 100%;
  }

  .skills-content,
  .contact-content {
    width: 100%;
    justify-content: center;
  }

  .contact-content {
    width: 100%;
    justify-content: center;
  }

  .left-content {
    justify-content: center;
    height: auto;
  }

  .skills {
    margin-bottom: 20vh;
    margin-top: 20vh;
  }

  .projects {
    margin-top: 20vh;
  }

  .skills-content,
  .contact-content {
    width: 100%;
    position: static;
    margin-top: 40px;
  }

  .skills,
  .contact {
    min-height: 0px;
  }

  .skills-content:before,
  .contact-content:before {
    padding-top: 0%;
  }
}

@media only screen and (max-width: 800px) {
  .header-content {
    height: 60vh;
  }

  header {
    max-height: 650px;
  }

  header {
    height: calc(100vh - 130px);
  }

  html {
    font-size: 14px;
  }
}

@media only screen and (max-width: 590px) {
  .header-content {
    justify-content: center;
  }

  .header-name {
    font-size: 2.3rem;
  }

  .subheader {
    font-size: 1.8rem;
  }
}
* {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="email"],
input[type="text"],
select:focus,
textarea {
  font-size: 16px;
  outline: none;
  box-shadow: none;
}

input[type="button"]:focus,
input[type="submit"]:focus {
  outline: none;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #04080f;
}

html {
  font-size: 16px;
}
